<template>
  <div class="address-list">
    <div class="reture" @click="retu">
      <img
        src="https://trade.m.xiaomiyoupin.com/youpin/static/m/res/images/icons/icon_arrow_left_black.png"
      />
    </div>
    <div class="address-title">地址管理</div>
    <van-address-list
      v-model="chosenAddressId"
      :list="list"
      default-tag-text="默认"
      @add="onAdd"
      class="sites"
      @edit="onEdit"
    /> 
  </div>
</template>

<script>
export default {
  data() {
    return {
      chosenAddressId: "1",
      list: [],
    };
  },
  created() {
    
    this.list = JSON.parse(sessionStorage.getItem("list"));
  },
  methods: {
    // 返回
    retu() {
      this.$router.go(-1);
    },
    // 新增地址
    onAdd() {
      sessionStorage.setItem("AddressTheEditor", "true");
      this.$router.push({ path: "/addresstheeditor" });
    },
    // 编辑已有地址
    onEdit(item, index) {
      
      

      item.addressDetail=item.address.split(' ')[1];
      sessionStorage.setItem("AddressTheEditor", JSON.stringify(item));
      sessionStorage.setItem("replaceIndex", index);
      this.$router.push({ path: "/addresstheeditor" });
    },
  },
};
</script>

<style lang="scss" scoped>
.address-list {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: white;
  z-index: 23;

  .reture {
    position: absolute;
    top: 0;
    left: 0;

    img {
      width: 39px;
      height: 48px;
    }
  }

  .address-title {
    font-size: 16px;
    color: #333;
    width: 100vw;
    height: 44px;
    line-height: 44px;
    text-align: center;
  }

  .sites {
    /deep/.van-radio__icon {
      display: none;
    }
  }
}
</style>